* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.bg_blend_of_home {
  height: 200vh;
  width: 100%;
  position: fixed;
  background: linear-gradient(180deg, #0e1391 0%, #0e0045 100%);
  z-index: -500;
}
.FAQMainContainer {
  width: 100%;
  height: 100vh;
  padding-top: 9.27rem;
}
.FAQContainer {
  width: 90%;
  max-height: 94%;
  background: rgba(255, 255, 255, 0.1);
  box-shadow: inset 349px 4px 250px rgba(255, 242, 250, 0.1);
  border-radius: 12px;
  margin: auto;
  overflow-y: scroll;
  scrollbar-width: none;
  -ms-overflow-style: none;
}
.FAQContainer::-webkit-scrollbar {
  display: none;
}

.FAQQuestion {
  background-color: transparent;
  padding: 10px;
  padding-left: 2rem;
  width: 100%;
  border: none;
  text-align: left;
  outline: none;
  transition: 0.4s;
  font-family: "Now";
  font-style: normal;
  font-size: 1rem;
  text-transform: uppercase;
  color: #bfbeff;
  /* padding: 2rem; */
  /* padding-bottom: 1rem; */
  display: flex;
  justify-content: space-between;
  font-weight: 500;
  align-items: center;
}
.FAQQuestion h2 {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  font-weight: 500;
}

.FAQArrowDown {
  cursor: pointer;
  width: 4rem;
}
.FAQAnswer {
  font-family: "Now";
  font-style: normal;
  font-weight: 500;
  font-size: 1rem;
  text-transform: uppercase;
  color: #cdcee6;
  padding-top: 0;
  background-color: transparent;
  max-height: 0rem;
  overflow: hidden;
  margin-left: 2rem;
  transition: max-height 0.4s ease-out;
  border-bottom: 1px solid #f8e6ff;
}
.FAQAnswer p {
  margin-bottom: 1rem;
}
.lastAnswer {
  border-bottom: none;
}
.ArrowRotate {
  animation-name: rotate_arrow_faq;
  animation-iteration-count: 1;
  animation-direction: alternate;
  animation-duration: 400ms;
  animation-fill-mode: forwards;
}
.ArrowRotate2 {
  animation-name: rotate_arrow_faq_2;
  animation-iteration-count: 1;
  animation-direction: alternate;
  animation-duration: 400ms;
  animation-fill-mode: forwards;
}
@keyframes rotate_arrow_faq {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(180deg);
  }
}
@keyframes rotate_arrow_faq_2 {
  from {
    transform: rotate(180deg);
  }
  to {
    transform: rotate(0deg);
  }
}
#bg_blend {
  height: 200vh;
  width: 100%;
  position: fixed;
  z-index: -100;
  background: linear-gradient(180deg, #0e1391 0%, #0e0045 100%);
}
@media only screen and (max-width: 1100px) {
  .FAQMainContainer {
    padding-top: 50px;
    height: 90vh;
  }
}
@media only screen and (max-width: 1000px) {
  .FAQQuestion {
    font-size: 0.8rem;
  }
  .FAQAnswer {
    font-size: 0.8rem;
  }
  .FAQArrowDown {
    width: 3.5rem;
  }
}
@media only screen and (max-width: 790px) {
  .FAQMainContainer {
    padding-top: 10px;
  }
}
@media only screen and (max-width: 750px) {
  .FAQMainContainer {
    padding-top: 50px;
  }
}
@media only screen and (max-width: 720px) {
  .FAQQuestion {
    font-size: 0.7rem;
  }
  .FAQAnswer {
    font-size: 0.7rem;
  }
  .FAQArrowDown {
    width: 3rem;
  }
  .FAQMainContainer {
    height: 75vh;
  }
}
@media only screen and (max-width: 475px) {
  .FAQQuestion {
    font-size: 0.6rem;
  }
  .FAQAnswer {
    font-size: 0.6rem;
  }
  .FAQArrowDown {
    width: 2rem;
  }
  .FAQMainContainer {
    height: 65vh;
  }
}
@media only screen and (max-width: 370px) {
  .FAQMainContainer {
    height: 55vh;
  }
  .FAQQuestion {
    font-size: 0.5rem;
    padding-left: 1rem;
  }
  .FAQAnswer {
    font-size: 0.5rem;
  }
  .FAQArrowDown {
    width: 1.7rem;
  }
  .FAQAnswer {
    margin-left: 1rem;
  }
}
@media only screen and (max-width: 300px) {
  .FAQQuestion {
    font-size: 0.4rem;
  }
  .FAQAnswer {
    font-size: 0.4rem;
  }
  .FAQArrowDown {
    width: 1.5rem;
  }
}
